.wcf-category-showcase-wrapper{
    display: grid;
    grid-template-columns: repeat(4,1fr);
}
.wcf-category-showcase-item{
    background-repeat: no-repeat;
    background-size: cover;
    padding: 0;
    position: relative;
}
.wcf-category-showcase-item::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}
.wcf-cs-thumb {
    position: relative;
}
.wcf-cs-content{
    position: relative;
}
.wcf-cs-icon {
    transform: translate(-50% ,-50%);
}
// title before
.wcf-cs-cat-title{
    color: #1e1e1e;
    position: relative;
    display: inline-block;
}
.wcf-cs-cat-title::before {
    content: "";
    position: absolute;
    left: -30px;
    top: 30px;
}

.wcf-cs-cat-title::after {
    content: "";
    position: absolute;
    right: -30px;
    top: 30px;
}

// slogan before 
.wcf-cs-cat-slogan{
    position: relative;
}
.wcf-cs-cat-slogan::after{
    content: "";
    position: absolute;
    right: 0;
    top: 9px;
    height: 5px;
    width: 5px;
    background-color: #f00;
}

// border style 
.wcf-cs-border-style{
    width: 100%;
}
.wcf-cs-cat-total{
    z-index: 2;
}